import { Routes, Route, useNavigate } from "react-router-dom";
import Navbar from "../components/Navbar";
import CocContainer from "../components/Coc";
import { Spotify } from "../components/Spotify";

function Projects() {
  return (
    <>
      <div>
        <Routes>
          <Route index element={<ProjectContent />} />
          <Route path="spotify" element={<Spotify />} />
          <Route path="coc" element={<CocContainer />} />
          <Route path="*" element={<div>Not found</div>} />
        </Routes>
      </div>
    </>
  );
}
function ProjectContent() {
  const navigate = useNavigate();
  return (
    <>
      <Navbar />
      <button onClick={() => navigate("/projects/spotify")}>spotify</button>
      <br />
      <button onClick={() => navigate("/projects/coc")}>coc</button>
    </>
  );
}

//<button onClick={() => navigate("/projects/coc", { replace: true })}>coc</button> replace true then no back

export default Projects;
