//import axios
import { useEffect, useState } from "react";
import axios from "axios";

const CocContainer = () => {
  const COCAPI =
    process.env.NODE_ENV !== "production"
      ? "http://localhost:8081/cocapi"
      : "https://shaquibdev-server.herokuapp.com/cocapi";

  const [data, setData] = useState([]);
  const [Loading, setLoading] = useState(true);

  useEffect(() => {
    Fetchapi();
  }, []);

  const Fetchapi = async () => {
    try {
      setLoading(true); // USE BEFORE FETCH
      await axios.get(COCAPI).then((response) => {
        setLoading(false); // SET LOADING FALSE AFTER GETTING DATA
        const allData = response.data;
        setData(allData);
        console.log(allData);
      });
    } catch (e) {
      setLoading(false); // ALSO SET LOADING FALSE IF ERROR
      console.log(e);
    }
  };
  return <div>{Loading ? "Loading Data..." : <CocSchema data={data} />}</div>;
};

const CocSchema = (props: any) => {
  return (
    <div className="container ">
      <h1>{props.data.name}</h1>
      <h1>{props.data.tag}</h1>
      <h1>{props.data?.clan?.name}</h1>
      <h1>{props.data?.troops?.[23]?.name}</h1>
    </div>
  );
};

export default CocContainer;
